*,
*::before,
*::after {
  box-sizing: inherit;
}

*::selection {
  color: #fff;
  background-color: transparent;
}

*::-moz-selection {
  color: #fff;
  background-color: transparent;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {

  min-height: 100vh;
  //max-width: $width-desktop-max;
  margin: 0 auto;

  font-family: $font;
  font-weight: 400;
  font-style: normal;
  //font-size: 14px;
  //line-height: 16px;
  display: flex;
  flex-direction: column;

  &.js__body-no-scroll {
    overflow: hidden;
  }
}

main {
  flex-grow: 1;
  width: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input::-ms-clear {
  display: none;
}

input:-moz-focusring {
  outline: none;
  border-color: #00adef;
  box-shadow: none;
}

input:invalid {
  outline: none;
  box-shadow: none;
}

ul li {
  list-style: none;
}

a {
  border-bottom: 0;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.wrapper {
  min-width: 320px;
  max-width: 1440px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;

  // @include desktop {
  //   padding-left: 30px;
  //   padding-right: 30px;
  // }

  // @include tablet {
  //   padding-left: 15px;
  //   padding-right: 15px;
  // }

  @include mobile {
    width: 100%;
  }
}

* {
  outline: none;
}

[type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner, button::-moz-focus-inner {
  outline: none;
}

button:focus-visible {
  outline: none;
}
